<template>
  <div class="hello">
    <!-- 遮罩视图 -->
    <div @click="type=false" v-show="type" id="bj" style="height:100vh;width:100vw;background:#000;position:fixed;left:0;top:0;z-index:200;">
      <div style="height:400px;position:absolute;left:0;top:50%;margin-top:-200px;overflow:hidden;">
        <img :src="imgs" alt="本张图片地址有问题,抱歉给您带来不便" style="font-size:30px;color:#fff;height:auto;width:100%;">
      </div>
    </div>
    <ul id="nav">
      <li v-for="(item, index) in navarr" :key="index" class="fl" style="flex:1;text-align:center;">
        <router-link :to="item.path"
          :class="path==item.path?'active':''">{{item.title}}</router-link>
      </li>
    </ul>
    <div id="no1">
      <div v-for="(item, index) in arr" :key="index" style="border-bottom:6px #f0f0f0 solid;">
          <!-- 头部 -->
          <div class="clearfix" style="padding:0 12px;height:60px;line-height:60px;border-bottom:1px #f0f0f0 solid;">
              <div class="fl"><img :src="item.header" style="margin-right:10px;vertical-align:middle;border-radius:50%;height:36px;width:36px;" alt=""></div>
              <p class="fl" style="font-size:14px;font-weight:550;">{{item.top_commentsName}}</p>
              <van-icon name="arrow-down"  class="fr" style="line-height:60px;font-size:16px;color:#666;"/>
          </div>
          <!-- 内容 -->
          <p style="padding:8px 12px; font-size:16px;max-height:200px;overflow:auto;">{{item.text}}</p>
          <div v-if="item.type=='image'" style="height:200px;width:100%;overflow:hidden;">
              <img @click="fang(item.image)" :src="item.image" alt="本张图片地址有问题,抱歉给您带来不便" style="font-size:30px;height:auto;width:100%;" >
          </div>
          <div v-if="item.type=='gif'" style="height:300px;width:100%;overflow:hidden;">
              <img :src="item.gif" alt="本张图片地址有问题,抱歉给您带来不便" style="font-size:30px;height:auto;width:100%;" >
          </div>
          <div v-if="item.type=='video'" style="height:400px;width:100%;overflow:hidden;">
              <video controls height="400" width="100%" :src="item.video"></video>
          </div>
          <!-- 底部 -->
          <div style="display:flex;padding:10px 12px;border-top:1px #f0f0f0 solid;">
              <p @click="item.n==0?item.n++:''" style="flex:1;font-size:14px;"><van-icon name="good-job-o" style="vertical-align:middle;padding:0 6px;font-size:22px;" />{{item.up +item.n}}</p>    
              <p @click="item.n=0" style="flex:1;font-size:14px;"><van-icon name="good-job-o" style="transform:rotate(90deg);vertical-align:middle;padding:0 6px;font-size:22px;" />踩</p>
              <p style="flex:1;font-size:14px;"><van-icon name="comment-o" style="vertical-align:middle;padding:0 6px;font-size:22px;" />{{item.comment}}</p>
              <p style="flex:1;font-size:14px;"><van-icon name="share" style="vertical-align:middle;padding:0 6px;font-size:22px;" />{{item.forward}}</p>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  created(){
    this.path=this.$route.path;
    this.$http.get(`https://www.apiopen.top/satinGodApi?type=${this.$route.params.id}&page=1`).then(res => {
      this.arr = res.data.data;
      for(var i=0;i<this.arr.length; i++){
        this.$set(this.arr[i],'n',0)
      }
    });
  },
  methods:{
    //遮罩  获取图片
    fang(img){
      console.log(img)
      if(img != ''){
        this.imgs=img;
        this.type=true;
      }
    }
  },
  watch: {
    '$route'(a,b){
      this.path=a.path;
      this.$http.get(`https://www.apiopen.top/satinGodApi?type=${a.params.id}&page=1`).then(res => {
        this.arr = res.data.data;
        for(var i=0;i<this.arr.length; i++){
          this.$set(this.arr[i],'n',0)
        }
      });
    }
  },
  data () {
    return {
      path:'',
      arr: "",
      num:0,
      k:0,
      imgs:'',
      type:false,
      navarr:[
        {
          title:'推荐',
          path:'/HelloWorld/1'
        },
        {
          title:'图片',
          path:'/HelloWorld/3'
        },
        {
          title:'视频',
          path:'/HelloWorld/5'
        },
        {
          title:'文字',
          path:'/HelloWorld/2'
        }
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  padding:80px 0;
}
#nav{
  height:80px;
  line-height:80px;
  background-color: #fff;
  display:flex;
  position:fixed;
  width:100%;
  z-index: 30;
  left:0;
  top:0;
  font-size:30px;
}
.active{
  color:tomato;
  font-weight:530;
  font-size:40px;
}
</style>



