<template>
  <div id="app">  
    <router-view/>
    <!-- 底部 -->
    <div class="footer">
      <van-tabbar
        v-model="active"
        active-color="#07c160"
        inactive-color="#000"
      >
        <van-tabbar-item icon="wap-home-o">首页</van-tabbar-item>
        <van-tabbar-item icon="underway-o">发现</van-tabbar-item>
        <div style="height:40px;width:40px;background:#ff8159;margin:auto 0;border-radius:50%;"> 
          <van-tabbar-item icon="plus" style="font-size:30px;margin-top:10px;color:#fff;"></van-tabbar-item>
        </div>
        <van-tabbar-item icon="chat-o">消息</van-tabbar-item>
        <van-tabbar-item icon="manager-o">我的</van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
      active:0,
    }
  },
  created(){
    if(this.$route.name=='find'){
      this.active=1;
    }
    if(this.$route.name=='info'){
      this.active=3;
    }
    if(this.$route.name=='min'){
      this.active=4;
    }
  },
  watch:{
    active(a,b){
      console.log(a)
      if(a==0){
        this.$router.push('/HelloWorld/1');
      }
      if(a==1){
        this.$router.push('/find');
      }
      if(a==3){
        this.$router.push('/info');
      }
      if(a==4){
        this.$router.push('/min');
      }
    }
  }
}
</script>

<style>
*{margin:0;padding:0; list-style-type: none;}
a{text-decoration: none; color:#000;}
.clearfix:after, .clearfix:before{content:'';display:table;}
.clearfix:after{clear:both;}
.clear{zoom:1;}
.fl{float:left;}
.fr{float:right;}
.van-tabbar{
  height:100px !important;
}
.van-tabbar-item__icon i{
  font-size:46px !important;
}
.van-tabbar-item__text{
  font-size:26px !important;
}
</style>



